.app-game {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}

.game {
  flex: 1 1 60%;
  display: flex;
  flex-direction: column;
}

.game-history {
  flex: 1 1 40%;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.board-content {
  flex: 1 1 80%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
}